<template>
    <div class="imgs">
        <img :class="['item', {'item-active': currentIndex === index}]" :src="item" 
            v-for="(item, index) in datas" :key="index" draggable="false" >
        <div class="mini-img" ref="wrapper">
            <ul ref="ul" @mousedown="handleMouseDown" 
                         @mousemove="handleMouseMove"
                         @touchstart='handleMouseDown'
                         @touchmove='handleMouseMove'>
                <li :class="['mini-item', {'mini-item-active': currentIndex === index}]" ref="item"
                    v-for="(item, index) in datas" :key="index" @click="changeIndex(index)">
                    <img :src="item" draggable="false" >
                </li>
            </ul>
        </div>
        <div class="ctrl">
            <div class="prev btn" @click="changeIndex(-1)">上一张</div>
            <p class="num" style="">{{currentIndex + 1}}/{{datas.length}}</p>
            <div class="last btn" @click="changeIndex(-2)">下一张</div>
            <div class="hide btn" @click="hideMini">{{isHide ? '显示' : '隐藏'}}</div>
        </div>
    </div>    
</template>
<script>
export default {
    props: {
        datas: {
            type: Array,
            default: () => ['http://www.cqdwq.com/UploadFile/20131017194410196.jpg']
        }
    },
    data () {
        return {
            currentIndex: 0,
            allWidth: 0,
            isDown: false,
            position: {
                lastX: 0
            },
            dragable: false,
            isHide: false,
            isClick: true
        }
    },
    mounted() {
        this.initWidth()
        window.addEventListener('mouseup', () => {
            this.handleMouseUp()
        })
        window.addEventListener('touchend', () => {
            this.handleMouseUp()
        })
    },
    methods: {
        hideMini () {
            if (this.isHide) {
                this.$refs.wrapper.style.height = 70 + 'px'
            } else {
                this.$refs.wrapper.style.height = 0 + 'px'
            }
            this.isHide = !this.isHide
        },
        changeIndex (n) {
            if (!this.isClick) {
                return
            }
            if (n === -1) {
                this.currentIndex--
            } else if (n === -2) {
                this.currentIndex++
            } else {
                this.currentIndex = n
            }
        },
        initWidth () {
            this.allWidth = 100 * this.datas.length + 4
            this.$refs.ul.style.width = this.allWidth +'px'
            this.i = 9 // 长度能展示9张图片
            if (window.innerWidth < 900) {
                this.i = Math.floor(window.innerWidth / 100)
            }
            if (this.datas.length >= this.i) {
                this.dragable = true
            }
        },
        handleMouseDown (e) {
            if (this.dragable) {
                this.isDown = true
            }
            let offset = 0
            let res = /[\d-]+/g.exec(this.$refs.ul.style.transform)
            if (res) {
                offset = Number(res[0])
            }
            if (e.clientX) {
                this.position.lastX = e.clientX - offset
            }
            if (e.touches) {
                this.position.lastX = e.touches[0].clientX - offset
            }
            this.$refs.ul.style.transition = ``
        },
        handleMouseMove (e) {
            if (this.isDown) {
                let offset
                if (e.clientX) {
                    offset = e.clientX - this.position.lastX
                }
                if (e.touches) {
                    offset = e.touches[0].clientX - this.position.lastX
                }
                this.$refs.ul.style.transform = `translateX(${offset}px)`
                // 区别拖拽和点击
                if (Math.abs(offset) > 10) {
                    this.isClick = false
                }
            }
        },
        handleMouseUp () {
            if (this.isDown) {
                this.isDown = false
                setTimeout(() => {
                    this.isClick = true
                }, 20)
                let offset = 0
                let res = /[\d-]+/g.exec(this.$refs.ul.style.transform)
                if (res) {
                    offset = Number(res[0])
                    this.goOffset(offset)
                }
            }
        },
        moveTo (x) {
            this.$refs.ul.style.transition = `all .5s`
            this.$refs.ul.style.transform = `translateX(${x}px)`
        },
        goOffset (offset, isIndex) {
            // 能拉的距离 ul的总长 - 可显示的总长
            let offsetRight = 900 - this.allWidth
            // console.log(this.i * 100, this.allWidth)
            if (window.innerWidth < 900) {
                // 响应式的适配
                offsetRight = window.innerWidth - this.allWidth
            }
            if (this.dragable) {
                if (offset > 0) {
                    // 向右拉过界
                    this.moveTo(0)
                } else if (offset < offsetRight) {
                    // 右拉过界
                    this.moveTo(offsetRight)
                } else if (isIndex){
                    this.moveTo(offset)
                }
            }
        }
    },
    watch: {
        currentIndex (index) {
            if (index == -1) {
                this.currentIndex = this.datas.length - 1
                return
            } else if (index == this.datas.length) {
                this.currentIndex = 0
                return
            }
            let curEl = this.$refs.item[index]
            if (curEl) {
                // 居中小图 // 距离左边 = 总的宽度 - 100（一张）/ 2
                this.goOffset((window.innerWidth - 100) / 2 - curEl.offsetLeft, true)
            }
        },
        datas () {
            this.initWidth()
        }
    }
}
</script>
<style lang="less" scoped>
.imgs{
    height: 100%;width: 100%;
    position: relative;
    .item{
        height: 100%;width: 100%;
        position: absolute;
        left: 0;top: 0;
        opacity: 0;
        transition: all .7s;
    }
    .item-active{
        opacity: 1;
    }
    .mini-img{
        position: absolute;
        transition: all .15s;
        left: 0;right: 0;bottom: 0;
        height: 70px;
        border: 1px solid #b99362;
        border-top: 0px;
        overflow: hidden;
        // transform: translateY(70px);
        ul{
            height: 100%;margin: 0 auto;
            .mini-item{
                box-sizing: border-box;
                float: left;
                height: 100%;width: 100px;
                background:#fdf6ec;
                padding: 4px 2px;
                cursor: pointer;
                img{
                    height: 100%;width: 100%;
                    border-radius: 3px;
                }
            }
            .mini-item-active{
                background: #e6a23c;
                padding: 4px; width: 104px;
            }
        }
    }
    .ctrl{
        position: absolute;
        bottom: 0;width: 100%;
        transform: translateY(65px);
        display: flex;
        justify-content: center;
        p{
            margin: 0 10px;
        }
        .btn{
            color: #e6a23c;
            border: 1px solid;
            padding: 0 8px;
            border-color: #e6a23c; 
            background-color: #fdf6ec;
            border-radius: 4px;
            cursor: pointer;
            &:hover{
                background: #e6a23c;
                color: white;
            }
        }
        .hide{
            transform: translateX(50px)
        }
        .num{
            width: 50px; 
            line-height: 23px;
            text-align: center;
        }
    }
}
</style>




