<template>
    <div class="img-reader">
        <h1 class="tt">{{image.title}}</h1>
        <div class="imgs-wrapper">
            <Imgs :datas='image.imgUrls'/>
        </div>
        <div class="art-wrapper">
            <div class="article">
                <div class="cont" v-html="image.content">

                </div>
                <div class="art-bottom">
                    {{image.create_time | date}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Imgs from '_c/imgs'
import Footer from '_c/footer'
import { getImage } from '@/api'
export default {
    data () {
        return {
            image: {}
        }
    },
    mounted() {
        this.getImg()
    },
    methods: {
        async getImg () {
            let { cateid, cateindex } = this.$route.query
            let res = await getImage(cateid, cateindex)
            this.image = res
        }
    },
    components: {
        Footer, Imgs
    },
    filters: {
        date (t) {
            t = t ? new Date(Number(t)) : new Date(1545977032592)
            let reg = /\//g
            return t.toLocaleDateString().replace(reg, '-')
        }
    }
}
</script>
<style lang="less" scoped>
.img-reader{
    .reader-header{
        height: 300px;
        overflow: hidden;
    }
    .title-wrapper{
        width: 1200px;
        margin: 0 auto;
        position: relative;
        .cur-category{
            text-align: right;
            font-size: 25px;
            font-weight: 700;
            color: #e0c073;
            padding: 10px;
            span{
                font-weight: 100;
                font-size: 22px;
                margin-right: 10px;
            }
        }
        ul{
            border-bottom: 1px solid #ccc;
            text-align: left;
            .item{
                margin-right: 5px;
                line-height: 50px;
                padding: 0 42px;
                display: inline-block;
                cursor: pointer;
                transition: all .5s;
                color: white;
                background-color: #e0c073;
            }
        }
    }
    .tt{
        color: #e0c073;
        padding-top: 30px;
    }
    .imgs-wrapper{
        width: 900px;
        height: 600px;
        margin: 60px auto;
    }
    .art-wrapper{
        width: 1200px;
        margin: 0 auto;
        overflow: hidden;
        .article{
            h1{
                margin: 20px 0;
                color: #e0c073;
                font-weight: normal;
            }
            .empty{
                padding: 1px 0 ;
            }
            .cont{
                padding: 40px 30px 50px 30px;
                text-align: left;
                text-indent: 2em;
                line-height: 45px;
                font-size: 17px;
            }
            .art-bottom{
                border-top: 1px solid #e0c073;
                padding: 20px;
                text-align: right;
            }
        }
    }
    .sec-footer{
        height: 120px;width: 100%;
        background-color: #e0c073;
    }
}
@media screen and(max-width: 1200px) {
    .img-reader {
        .art-wrapper{
            width: 100%;
        }
    }
}
@media screen and(max-width: 900px) {
    .img-reader {
        .imgs-wrapper {
            width: 100%;
            height: 550px;
        }
    }
}
@media screen and(max-width: 700px) {
    .img-reader {
        .imgs-wrapper {
            height: 400px;
        }
    }
}
@media screen and(max-width: 500px) {
    .img-reader {
        .imgs-wrapper {
            height: 300px;
        }
    }
}
@media screen and(max-width: 400px) {
    .img-reader {
        .imgs-wrapper {
            height: 250px;
        }
    }
}
</style>





